import React from 'react';
import styled from 'styled-components'

const Container = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 60px;
  position: fixed;
  justify-content: space-between;
  margin: 0;
  top: 60px;
  background-color: #E5E5E5;
  overflow:hidden;
  padding: 10px 16%;

  @media (max-width: 780px) {
    padding: 10px 8%;
  }
`
